import { combineReducers } from 'redux'

let response_default = [{
	name: null,
	event: [
		{
			title: null
		}
	]
}]

const browsing_mode = (state = 'list', action) => {
	switch (action.type) {
		case 'TOGGLE_MODE':
			return null
		default:
			return state
  }
}

const data = (state = response_default, action) => {
	switch (action.type) {
		case 'DOWNLOAD_DATA':
			return action.raw_data
		default:
			return state
	}
}


const data_now = (state = {index_shop: 0, index_coupon: 0}, action) => {
	switch (action.type) {
		case 'SELECT_SHOP':
			return Object.assign({}, state, {
        visibilityFilter: action.filter
      })
			{index_shop: action.index}

		case 'SELECT_COUPON':
			return {index_coupon: action.index}
		default:
			return state
  }
}

const page_now = (state = 'shop_list', action) => {
	switch (action.type) {
		case 'PREVIOUS_PAGE':
			switch (state) {
				case shop_list:
					return
				case coupon_list:
					return
				case coupon_content:
					return
				default:
					return state
			}
		case 'NEXT_PAGE':
			switch (state) {
				case shop_list:
					return
				case coupon_list:
					return
				case coupon_content:
					return
				default:
					return state
			}
		default:
			return state
	}
}



export default combineReducers({
  browsing_mode,
  data,
  data_now,
  page_now
})


